<template>
  <transition name="fade" mode="in-out">
    <div class='rendezvousPage'>
      <v-Header class="head">
        <span slot='text'>{{contType==3?'约':contType==4?'装备':contType==5?'教程':''}}</span>
      </v-Header>
      <div class='header' v-if="contType==3">
        <ul>
          <template v-for="(row,$index) in tabList">
            <li :class="{show:select===$index}" @click='changeType($index)' :key='$index'>{{row}}</li>
          </template>
        </ul>  
      </div>
      <div id='listsScrollCon' :class="['listsScrollCon',contType!=3&&'noList']">
        <ul>
          <div class="refreshTips" v-show="refreshTips"><img src="../../assets/images/loading.gif" alt=""> 正在刷新...</div>
          <v-rendezvousPage :Lists='Lists' />
          <div class="loadmoreTips" v-if="!refreshTips&&!allLoaded&&isloading"><img src="../../assets/images/loading.gif" alt=""> 加载中...</div>
          <div class="loadmoreTips" v-if='allLoaded'>{{this.Lists.length>0?'已经加载到底部啦^_^':'暂无相关文章信息^_^'}}</div>
        </ul>
      </div>
      <div class='publishBtn' @click='publish'>发布</div>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header';
  import BScroll from 'better-scroll';
  import rendezvousPage from './rendezvousList.vue'
  export default {
    name: '',
    components: {
      'v-Header':Header,
      'v-rendezvousPage':rendezvousPage,
    },
    props: {},
    data () {
      return {
        scroll:'',
        isloading:false,
        tabList:['全部','俱乐部约','教练约','达人约'],
        select:0,
        page:0,
        refreshTips: false,
        listUpdate: false,
        allLoaded: false,
        Loaded:false,
        lastProductId:0,
        userType:0,
        Lists:[],
        contType:this.$route.params.id,
      }
    },
    watch: {

    },
    computed: {},
    mounted () {
      const wrapper = document.querySelector('#listsScrollCon');
      this.scroll = new BScroll(wrapper, {
        click: true,
        pullDownRefresh: true,
        pullUpLoad: true,
      });
      this.scroll.on('pullingDown', () => {
        this.refreshTips = true;
        this.resetList();
      });
      this.scroll.on('pullingUp', () => {
        if(!this.allLoaded){
          this.loadMore();
          this.isloading=true;
        }
      });
      this.resetList();
    },
    methods: {
      // ARTICLELIST_PROT
      getList() {
        if(!this.isloading&&this.allLoaded) return;
        this.$http.get(this.$port.ARTICLELIST_PROT,{
          params:{
            contType:this.$route.params.id,
            userType:this.userType,
            lastProductId:this.lastProductId
          }
        }).then((response) => {
          const {data} = response;
          this.isloading = false;
          if(response.code == 200){
            this.listUpdate = true;
            if(data.length==0){
              this.allLoaded=true;
            }else{
              this.Lists=this.Lists.concat(data);
            }
            this.lastProductId = data.pop().productId;
          }
        })
        .catch((error) => {
          console.log(error.response);
        });
			},
      changeType(data){
          // console.log(e.target)
          this.select=data;
          switch (this.select) {
            case 0:
              this.userType = 0;
              break;
            case 1:
              this.userType = 4;
              break;
            case 2:
              this.userType = 3;
              break;
            case 3:
              this.userType = 2;
              break;
          
            default:
              break;
          }
          this.resetList();
      },
      publish(){
        this.$router.push({path:'/write/cover',query:{contType:this.contType}});
      },
      resetList() {
        // 列表初始化
        this.Lists=[];
        this.lastProductId = 0;
        this.allLoaded = false;
        this.isloading = true;
        this.getList();
        document.querySelector('#listsScrollCon').scrollTop = 0;
      },
      loadMore() {
        // 上拉加载更多
        this.getList();
      }
    },
    updated() {
      this.scroll.refresh();
      if (this.listUpdate) {
        this.isloading = false;
        this.refreshTips = false;
        this.listUpdate = false;
        this.scroll.finishPullDown();
        this.scroll.finishPullUp();
        
      }
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  .rendezvousPage{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    background:white;
    .header{
      z-index:1;
      background: white;
      position: fixed;
      top: 0.82rem;
      left: 0;
      width:7.5rem;
      height:0.86rem;
      color:#666666;
      font-size:0.32rem;
      line-height:0.86rem;
      border-bottom: 0.01rem solid rgb(229,229,229);
      ul{
        width:100%;
        height:100%;
        display:flex;
        li{
          flex: 1;
          text-align: center;
          &.show{
            color:#1797c6;
            border-bottom: 0.01rem solid #1797c6;
          }
        }
      }
    }
    .listsScrollCon{
      position: absolute;
      top:1.68rem;
      left:0;
      right:0;
      bottom:0.88rem;
      margin:auto;
      overflow:hidden;
      &.noList{
        top:0.82rem;
      }
    }
    .publishBtn{
      width:100%;
      height:0.88rem;
      position:fixed;
      bottom:0;
      left:0;
      background:url('../../assets/images/publishBtn.jpg') no-repeat;
      background-size:cover;
      color:white;
      line-height:0.88rem;
      font-size:0.36rem;
      text-align:center;
    }
  }
  .refreshTips {
    font-size:0.28rem;
    padding: 0.28rem;
    margin-bottom:-0.5rem;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .refreshTips>img{
    width: 0.5rem;
  }
  .loadmoreTips {
       font-size:0.28rem;
    padding: 0.28rem;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .loadmoreTips>img{
    width: 0.5rem;
  }
</style>


